/* You can add global styles to this file, and also import other style files */
@import "tailwindcss/base";
@import "tailwindcss/components";
@import "tailwindcss/utilities";

@import "prismjs/themes/prism-tomorrow";
@import "prismjs/plugins/toolbar/prism-toolbar";

@import "https://fonts.googleapis.com/css?family=Dosis:300,400,500,600,700";

@import '~@angular/cdk/overlay-prebuilt.css';

:root {
  /* --primary: theme('colors.purple.600');
  --secondary: theme('colors.yellow.600'); */
  --primary: #00aaaa;
  --secondary: #4a4744;
  --background-color: theme("colors.white");
  --text-color: #444;
  --text-color-contrast: theme("colors.white");
  --gray-light: theme("colors.gray.500");
  --gray-medium: theme("colors.gray.600");
  --gray-dark: theme("colors.gray.700");
  --table-even-row: theme("colors.gray.200");
  --shadow: theme("boxShadow.lg");
  --text-link-color: theme("colors.blue.500");
}

.btn {
  @apply relative py-1 inline-block text-center bg-transparent border-2 border-oreilly-900 rounded-md shadow-btn text-primary-500
  transition duration-300 ease-in-out uppercase font-dosis cursor-pointer;
  &:hover,
  &--active {
    top: 2px;
    left: 2px;
    box-shadow: 2px 2px 0 transparent;
  }
  &.disabled {
    @apply bg-gray-100 border-gray-400 text-gray-400;
    box-shadow: 0px 0px 0 transparent;
    &:hover,
    &--active {
      top: 0;
      left: 0;
    }
  }
}
